.abouttile{
    background-color: white;
    border-radius: 15px;
    display: flex;
    padding: 5px;

    box-shadow: 1px 1px 3px 1px #e0dede;
}

.aboutimage {
    width: 75px;
    height: 75px;
}

.abouttitle {
    display: flex;
    flex-direction: column;
    padding: 0px 5px;
    font-size: calc(15px + 0.390625vw);
}

.aboutsubtitle {
    display: flex;
    flex-direction: column;
    padding: 0px 5px;
    font-size: calc(10px + 0.390625vw);
    color: gray;
}

.textstack {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.aboutlist {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
}
