.hobbytile{
    background-color: white;
    border-radius: 5px;
    display: flex;
    padding: 5px;

    box-shadow: 1px 1px 3px 1px #e0dede;
}

.hobbytitle {
    display: flex;
    flex-direction: column;
    padding: 0px 5px;
    font-size: calc(10px + 0.390625vw);
}

.hobbieslist {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
}